import React, { Component } from 'react';
import { connect } from 'react-redux';
import { toogleTodo, deleteTodo } from '@/actions';
import Dialog from '@/component/Dialog/Dialog';
import styles from './todo-list.less';
class TodoList extends Component {
  state = {
    show: false,
    key: '',
  };
  deleteHandler = index => {
    this.setState({
      show: true,
      key: index,
    });
  };
  confirmHandler = () => {
    this.props.deleteTodo(this.state.key);
    this.setState({ show: false });
  };
  cancelHandler = () => {
    this.setState({ show: false });
  };
  render() {
    return (
      <>
        <ul>
          {this.props.todos.map((item, index) => (
            <li key={item.id} className={styles.list}>
              <span
                onClick={() => {
                  this.props.toogleTodo(index);
                }}
                className={`iconfont ${styles.check} ${item.completed ? 'icon-checkbox-fill' : 'icon-checkbox-empty'}`}
                style={{ color: item.completed ? '#f60' : '' }}
              />
              <span className={styles.content} style={item.completed ? { textDecoration: 'line-through' } : null}>
                {item.todo}
              </span>
              <span
                onClick={e => {
                  this.deleteHandler(index);
                }}
                className={`iconfont icon-close ${styles.close}`}
              />
            </li>
          ))}
        </ul>
        <Dialog
          visible={this.state.show}
          width={`60vw`}
          height={`40vw`}
          onConfirm={this.confirmHandler}
          onCancel={this.cancelHandler}
          title="确定要删除吗？"
        />
      </>
    );
  }
}
function getTodos(todos, filter) {
  switch (filter) {
    case 'All':
      return todos;
    case 'Completed':
      return todos.filter(item => item.completed);
    case 'Active':
      return todos.filter(item => !item.completed);
    default:
      break;
  }
}
const mapStateToProps = ({ todos, filter }) => ({
  todos: getTodos(todos, filter),
});
const mapDispatchToProps = {
  toogleTodo,
  deleteTodo,
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TodoList);
