import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './dialog.css';

export default class Dialog extends Component {
  static defaultProps = {
    width: 500,
    height: 300,
    Zindex: 1000,
    title: '提示',
    footerVisible: true,
    cancelText: '取消',
    cancelClass: {},
    okText: '确定',
    okClass: {},
  };

  static propTypes = {
    align: PropTypes.oneOf(['right', 'center', 'left', 'top', 'bottom']),
    visible: PropTypes.bool.isRequired,
    Zindex: PropTypes.number,
    title: PropTypes.string,
    footerVisible: PropTypes.bool,
    cancelText: PropTypes.string,
    cancelClass: PropTypes.object,
    okText: PropTypes.string,
    okClass: PropTypes.object,
    onCancel: PropTypes.func, //取消按钮回调
    onConfirm: PropTypes.func, // 确认按钮回调
    onOpen: PropTypes.func, // 弹框打开时的回调
  };

  constructor(props) {
    super(props);
    this.dialogRef = null;
  }

  state = {
    contentStyle: {
      width: this.props.width,
      height: this.props.height,
      verticalAlign:
        typeof this.props.align === 'undefined'
          ? 'middle'
          : ['top', 'bottom'].includes(this.props.align)
            ? this.props.align
            : 'middle',
    },
    containerStyle: {
      textAlign:
        typeof this.props.align === 'undefined'
          ? 'center'
          : ['left', 'center', 'right'].includes(this.props.align)
            ? this.props.align
            : 'center',
    },
  };

  closeHandler = event => {
    const { onCancel } = this.props;
    const iscloseBtn = event.target.dataset.id === 'closeBtn';
    if (this.dialogRef === event.target || iscloseBtn) {
      onCancel && onCancel();
    }
  };

  cancelHandler = event => {
    const { onCancel } = this.props;
    onCancel && onCancel();
  };

  confirmHandler = event => {
    const { onConfirm } = this.props;
    onConfirm && onConfirm();
  };

  componentDidMount() {
    const { onOpen } = this.props;
    onOpen && onOpen();
  }

  render() {
    const { cancelText, cancelClass, okText, okClass, title, footerVisible } = this.props;
    const { contentStyle, containerStyle } = this.state;
    const footer = (
      <div className="footer">
        <button className="btn" style={cancelClass} onClick={this.cancelHandler}>
          {cancelText}
        </button>
        <button className="btn primary ml15" style={okClass} onClick={this.confirmHandler}>
          {okText}
        </button>
      </div>
    );
    const elements = (
      <div
        className="dialog-container"
        ref={el => (this.dialogRef = el)}
        onClick={this.closeHandler}
        style={containerStyle}
      >
        <div className="dialog-content" style={contentStyle}>
          <div className="header">
            <span className="title">{title}</span>
            <span className="iconfont icon-guanbijiantou close-btn" data-id="closeBtn" />
          </div>
          <div className="content">{this.props.children}</div>
          {footerVisible && footer}
        </div>
      </div>
    );
    return this.props.visible && elements;
  }
}
