import React, { Component } from 'react';
import { connect } from 'react-redux';
import { setFilter } from '@/actions';
import styles from './footer.less';
class TodoFooter extends Component {
  state = {
    text: '',
  };
  select(item) {
    this.setState({
      text: item,
    });
    this.props.setFilter(item);
  }
  render() {
    return (
      !!this.props.length && (
        <div>
          {['All', 'Completed', 'Active'].map((item, index) => (
            <span
              key={index}
              onClick={() => {
                this.select(item);
              }}
              className={`${item === this.state.text ? styles.active : ''} ${styles.select}`}
            >
              {item}
            </span>
          ))}
        </div>
      )
    );
  }
}
const mapStateToProps = state => ({
  length: state.todos.length,
});
const mapDispatchToProps = {
  setFilter,
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TodoFooter);
