import React, { Component } from 'react';
import { connect } from 'react-redux';
import { addTodo } from '@/actions';
import styles from './todo-header.less'
class TodoHeader extends Component {
  state = {
    todo: '',
  };
  submitHandler = e => {
    e.preventDefault();
    const { todo } = this.state;
    const toodItem = {
      todo,
      id: Math.random()
        .toString(16)
        .substring(2),
      completed: false,
    };
    this.props.dispatch(addTodo(toodItem));
    this.setState({
        todo: ''
    })
  };
  changeHandler = e => {
    this.setState({
      todo: e.target.value,
    });
  };
  render() {
    return (
      <form onSubmit={this.submitHandler}>
        <input type="text" placeholder="What would you to be done ?" 
          className={styles.todoInput} value={this.state.todo} onChange={this.changeHandler} />
      </form>
    );
  }
}
export default connect()(TodoHeader);
